header {
  box-shadow: 0 0 7px #004992;
  min-width: 340px;
  background-color: #eff2f6; }

.navBarArea {
  background: linear-gradient(rgba(84, 116, 143, 0.75), rgba(83, 106, 124, 0.95), #536a7c, rgba(65, 82, 97, 0.95));
  display: flex;
  border-top: 1px solid #ffffff;
  padding: 3px 0; }

.navLink, .basketNavLink {
  color: #ffffff;
  padding: 10px 10px 10px 10px;
  margin-bottom: 0!important;
  margin-right: 1px!important;
  transition: all 0.5s; }


.responsiveNavLinkArea {
  color: #ffffff;
  transition: 0.5s;
  text-decoration: none!important;
  position: absolute;
  right: 10px; }

.basketNavLink, .contactNavLink {
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 20px;
  margin-right: 15px;
  margin-top: 3px;
  padding: 10px 13px 5px 13px;
  display: flex;
  align-content: baseline;
  width: 70px;
  background: linear-gradient(rgba(82, 102, 121, 0.95), #708995, rgba(82, 102, 121, 0.95)); }

.basketNavLink i, .contactNavLink i {
  height: 15px;
  transition: 0.5s;
  color: #c1f3d1;
  transform: scale(1.4); }

.basketNavLink p {
  width: 25px;
  height: 25px;
  transition: 0.5s;
  color: #ffffff;
  position: absolute;
  top: 12px;
  right: 15px;
  text-align: center;
  font-weight: bold;
  padding-left: 5px; }

.navLink:hover, .navActivLink {
  text-decoration: none;
  color: #e0ffeb;
  background: linear-gradient( rgb(109, 128, 156), rgb(126, 158, 189), rgb(87, 102, 127)); }

.basketNavLink:hover, .basketNavActiveLink, .contactNavLink:hover, .contactNavActiveLink {
  text-decoration: none;
  color: #e0ffeb;
  background: linear-gradient( rgb(109, 128, 156), rgb(111, 151, 115), rgb(87, 102, 127)); }

.navBar {
  margin: 0 auto!important;
  padding: 0 !important;
  width: 100%; }

.contactNavLink {
 display: none; }


@media (max-width: 535px) {

  header {
    margin-bottom: 5px; }

  .navBarArea {
    position: fixed;
    top: 70px;
    width: 100%;
    height: 54px;
    border-top: 1px solid #ffffff;
    padding: 3px 0; }

  .linkHidden {
    display: none; }

  .contactNavLink {
    border-radius: 10px;
    margin-right: 15px;
    margin-top: 2px;
    width: 40px;
    display: flex;
    justify-content: center; } }
